*, *::before, *::after {
margin: 0;
padding: 0;
box-sizing: border-box; 
font-family: 'Halcom', serif;
background-color: var(--bg-color);
color: #feb9c9;
}

:root {
    --bg-color: #18374c;
    --font-color: #feb9c9;
    --additional-color: #b31b27;
}

.h1 {
font-family: halcom, sans-serif;
font-weight: 400;
font-style: normal;

}

@media screen and (max-width: 500px) {

.message-media-query {
    display: none;
}
.header {
    width: 100vw;
    height: auto;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    grid-template-rows: 1fr 0.1fr auto 0.1fr;
}

.header__image {
    width: 100vw;
    height: auto;
    grid-column: 1/4;
    grid-row: 1/2;
}

.header__text {
    height: auto;
    color: #feb9c9;
    grid-column: 2/3;
    grid-row: 3/4;
    text-align: center;
    line-height: 115%;
}

.entry-form__form {
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    grid-template-rows: 1fr 0.1fr 1fr 0.1fr 1fr 0.1fr 1fr 0.1fr 1fr 0.1fr 1fr 0.1fr 1fr 0.1fr;
    overflow: hidden;
    text-align: center;
}

.entry-form__image {
    width: 100vw;
    height: auto;
    grid-row: 1/2;
    grid-column: 1/4;
}

.entry-form__headline {
    grid-row: 3/4;
    grid-column: 2/3;
}

.entry-form__first-name {
    border: none;
    border-bottom: dashed var(--font-color) 1px;
    grid-row: 5/6;
    grid-column: 2/3;
}

.entry-form__last-name {
    border: none;
    border-bottom: dashed var(--font-color) 1px;
    grid-row: 7/8;
    grid-column: 2/3;
}

.entry-form__e-mail {
    border: none;
    border-bottom: dashed var(--font-color) 1px;
    grid-row: 9/10;
    grid-column: 2/3;
}

.entry-form__date-of-birth {
    display: inline-block;
    grid-row: 11/15;
    grid-column: 2/3;

}

.entry-form__date-of-birth__DD__label {
    display: inline-block;

}































.entry-form__gender-toggle {
    grid-row: 15/16;
    grid-column: 2/3;
}

.entry-form__gender-toggle__female {
    grid-row: 15/16;
    grid-column: 2/3;
}

.entry-form__gender-toggle__male {
    grid-row: 15/16;
    grid-column: 2/3;
}

.entry-form__checkbox__terms-conditions {
    grid-row: 17/18;
    grid-column: 2/3;
}

.entry-form__checkbox__terms-conditions__label {
    grid-row: 17/18;
    grid-column: 2/3;
}
.entry-form__checkbox__mailing-list {
    grid-row: 19/20;
    grid-column: 2/3;
}

.entry-form__checkbox__mailing-list__label {
    grid-row: 21/22;
    grid-column: 2/3;
}

.entry-form__submit {
    grid-row: 23/24;
    grid-column: 2/3;

}


}


@media screen and (min-width: 501px) {

.header {
    display: none;
}
.entry-form {
    display: none;
}
.message-media-query {
    font-size: large;
    background-color: var(--bg-color);
    color: var(--font-color);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vw;
}

}